import { BrowserModule } from '@angular/platform-browser';
import { NgModule ,CUSTOM_ELEMENTS_SCHEMA} from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import {RouterModule,Routes} from '@angular/router';
import {FlashMessagesModule} from 'angular2-flash-messages';
import { ReactiveFormsModule  } from '@angular/forms';
import { Ng2Bs3ModalModule } from 'ng2-bs3-modal/ng2-bs3-modal';

import { AppComponent } from './app.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { HomeComponent } from './components/home/home.component';
import { ProfileComponent } from './components/profile/profile.component';
import { ForgottenPasswordComponent } from './components/forgotten-password/forgotten-password.component';
import { CategoryComponent } from './components/category/category.component';
import { AdminDashboardComponent } from './components/admin-dashboard/admin-dashboard.component';
import { CustomerDashboardComponent } from './components/customer-dashboard/customer-dashboard.component';


import {ValidateService} from './services/validate.service';
import {AuthService} from './services/auth.service';
import {SareeService} from './services/saree.service';
import {CartService} from './services/cart.service';
import { AddNewSareeComponent } from './components/add-new-saree/add-new-saree.component';
import { ShoppingCartComponent } from './components/shopping-cart/shopping-cart.component';


const appRoutes:Routes=[
  {path:'',component:HomeComponent},
  {path:'register',component:RegisterComponent},
  {path:'login',component:LoginComponent},
  {path:'profile',component:ProfileComponent},
  {path:'dashboard',component:DashboardComponent},
  {path:'forgotten-password',component:ForgottenPasswordComponent},
  {path:'categories',component:CategoryComponent},
  {path:'admin-dashboard',component:AdminDashboardComponent},
  {path:'customer-dashboard',component:CustomerDashboardComponent},
  {path:'addnewsaree',component:AddNewSareeComponent},
  {path:'shopping-cart',component:ShoppingCartComponent}
]


@NgModule({
  declarations: [
    AppComponent,
    DashboardComponent,
    LoginComponent,
    RegisterComponent,
    HomeComponent,
    ProfileComponent,
    ForgottenPasswordComponent,
    CategoryComponent,
    AdminDashboardComponent,
    CustomerDashboardComponent,
    AddNewSareeComponent,
    ShoppingCartComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    Ng2Bs3ModalModule,
    ReactiveFormsModule,
    HttpModule,
    FlashMessagesModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [ValidateService,AuthService,SareeService,CartService],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
  bootstrap: [AppComponent]
})
export class AppModule { }



