import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { Location } from '@angular/common';
import {FlashMessagesService} from 'angular2-flash-messages';
import {CartService} from '../../services/cart.service';
import {Router} from '@angular/router';
import { Ng2Bs3ModalModule } from 'ng2-bs3-modal/ng2-bs3-modal';
import 'jquery';
import 'bootstrap';


@Component({
  selector: 'app-shopping-cart',
  templateUrl: './shopping-cart.component.html',
  styleUrls: ['./shopping-cart.component.css']
})
export class ShoppingCartComponent implements OnInit {

  qty:Number;
	private selectedsarees = [];

	private isDataLoading = true;	
		private isLoading = true;
	private selectedsaree = {};
	private sareeQty=1;
	private amount = {};
  private redirectURL;
  // private totalamt;

private saree = {};
private name="";
private image="";
private price="";
//amount:Number;

  constructor(private http:Http,private cartService:CartService,
  	private flashMessage:FlashMessagesService,private router:Router,private location:Location) { 

  }

  ngOnInit() {
  	this.isDataLoading = true;
  	this.selectedsarees = [];
	this.getSelectedSarees();
	this.getTotalAmount();
  }

  getSelectedSarees() {
        this.cartService.getSelectedSarees().subscribe(
            data => {
			this.selectedsarees = data;
			this.getTotalAmount();
			},error=>console.log(error),
			() => this.isDataLoading = false);
    }

  deleteSaree(saree){
  	if (window.confirm("Are you sure you want to permanently delete this item?")) {
  		this.selectedsaree={id:saree.id,qty:saree.qty};
  		this.cartService.deleteSaree(this.selectedsaree).subscribe(
  			res=>{
  				this.flashMessage.show("Item deleted Suceesfully",{cssClass:'alert-success',timeout:3000});
  				this.getSelectedSarees();
  				this.getTotalAmount();
  				
  			},
  			error=>console.log(error)
  		);
  	}

  }

  loadSaree(saree) {
        this.qty   = saree.qty;
        this.name  = saree.info.name;
        this.image = saree.info.image;
        this.price = saree.info.price;
        this.saree = saree;
    }

  updateSaree(saree){
  	
    console.log("product in updateProduct",saree);
  	this.selectedsaree={id:saree.id,oldqty:saree.qty,newqty:this.qty};
    console.log("product in updateProduct selectedproduct",this.selectedsaree);
  	this.cartService.updateSaree(this.selectedsaree).subscribe(
  		res=>{
  			this.flashMessage.show("Item Edited Suceesfully",{cssClass:'alert-success',timeout:3000});
  			this.getSelectedSarees();
  			this.getTotalAmount();
  		},
  		error=>console.log(error)
  	);
  }

  

  getTotalAmount() {
        this.cartService.getTotal().subscribe(
            data => this.amount = data,
            error => console.log(error),
            () => this.isLoading = false
            );
  }



    onCheckout(selectedsarees,amount){
      const basketDetails={
        amount:this.amount,
        items:this.selectedsarees
      }

      //console.log("onCheckout method selectedsarees",selectedsarees);
      console.log("onCheckout method",basketDetails);
      this.cartService.paymentcheckout(basketDetails).subscribe(data=>{
        console.log("data paymentcheckout",data.href);
        this.redirectURL=data.href;
         //console.log("data paymentcheckout href",data[0].href);
        window.location.href=this.redirectURL;
        //this.flashMessage.show("payment done Suceesfully",{cssClass:'alert-success',timeout:3000});
        //console.log("payment done Suceesfully");
      },
      error=>console.log(error)
      );

    }

}



