import { Component, OnInit } from '@angular/core';
import {FlashMessagesService} from 'angular2-flash-messages';
import {SareeService} from '../../services/saree.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-add-new-saree',
  templateUrl: './add-new-saree.component.html',
  styleUrls: ['./add-new-saree.component.css']
})
export class AddNewSareeComponent implements OnInit {

  sareename:String;
  descr:String;
  category:String;
  arr=[];
  path:String;
  // data:String;
  // contentType:String;
  price:String;
  cat:String;
  image: String;
  private categories=[];


  filesToUpload: Array<File>;

  constructor(private flashMessage:FlashMessagesService,
  		      private sareeService:SareeService,
  		      private router:Router) { 
    this.filesToUpload = [];

  }
 

  ngOnInit() {
  	this.sareeService.getCategories().subscribe(data=>{
      //console.log("this.categories",data.category[0]);
			this.categories=data.category;	
		});

  }

  onchange(val){
  	this.cat=val;
  }


  onSareeSubmit(){

    // const image={
    //   data:this.data,
    //   contentType:this.contentType
    // }
  	const saree={
  		name:this.sareename,
  		description:this.descr,
  		category:this.cat,
  		image:this.image,
  		price:this.price
  	}
  	
    console.log("onSareeSubmit",saree);
  	//Register User
  	this.sareeService.addnewSaree(saree).subscribe( data=>{
      console.log("onsareesubmit",data)
  		if(data.success){
  			this.flashMessage.show("Saree added successfully,,!! ",{cssClass:'alert-success',timeout:3000});
  			this.router.navigate(['/admin-dashboard']);
  		}else{
  			this.flashMessage.show("Something went wrong..!!",{cssClass:'alert-danger',timeout:3000});
  			this.router.navigate(['/addnewsaree']);
  		
  		}
  	});

  }


    upload() {
        this.makeFileRequest("http://localhost:3000/upload", [], this.filesToUpload).then((result) => {
            //console.log("uploded image",result[0]);

            this.path=result[0].path;
            this.arr=this.path.split('public/');
            this.image=this.arr[1];
            console.log("uploded path",this.path);
            console.log("uploded image",this.arr);
            console.log("uploded image",this.image);
            //this.image='uploads/' + result[0].originalname;
            //this.image=result[0].path;

             //console.log("uploded image",this.image);
            //this.data=result[0].path;
            //product.img1.data = fs.readFileSync(imgPath);
            //this.contentType = result[0].mimetype;
        }, (error) => {
            console.error(error);
        });
    }

    fileChangeEvent(fileInput: any){
        this.filesToUpload = <Array<File>> fileInput.target.files;
    }

    makeFileRequest(url: string, params: Array<string>, files: Array<File>) {
        return new Promise((resolve, reject) => {
            var formData: any = new FormData();
            var xhr = new XMLHttpRequest();
            for(var i = 0; i < files.length; i++) {
                formData.append("uploads[]", files[i], files[i].name);
            }
            xhr.onreadystatechange = function () {
                if (xhr.readyState == 4) {
                    if (xhr.status == 200) {
                        resolve(JSON.parse(xhr.response));
                    } else {
                        reject(xhr.response);
                    }
                }
            }
            xhr.open("POST", url, true);
            xhr.send(formData);
        });
    }

}



