import { Component, OnInit } from '@angular/core';
import {SareeService} from '../../services/saree.service';
import {FlashMessagesService} from 'angular2-flash-messages';
import {CartService} from '../../services/cart.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-customer-dashboard',
  templateUrl: './customer-dashboard.component.html',
  styleUrls: ['./customer-dashboard.component.css']
})
export class CustomerDashboardComponent implements OnInit {


  sarees: Object;
  private selectedsaree={};
  private sareeQty = 1;

  constructor(private sareeService:SareeService,
  			  private router:Router,private cartService:CartService,private flashMessage:FlashMessagesService) { }

  ngOnInit() {

  	this.sareeService.getSarees().subscribe(data=>{
  		//console.log(data);
  		this.sarees=data.saree;
  		//console.log(this.sarees);
  	},
  	err=>{
  		console.log(err);
  		return false;
  	});
  }
  addToCart(saree){
   
    this.selectedsaree = { id: saree._id, qty: this.sareeQty,price:saree.price };

    this.cartService.addToCart(this.selectedsaree).subscribe(data=>{
      this.sareeQty = 1;
      this.flashMessage.show("Sraee added to cart",{cssClass:'alert-success',timeout:3000});
      
    },error=>console.log(error));

  }

}



